/* Calendar.css */
#svg-container {
  display: inline-block;
  max-width: 100%;
  overflow-x: auto; /* 支援多季節或寬 SVG 滾動 */
  margin: 1.5em 0;
}

svg.chart {
  width: 100%;
  height: auto;
}

.bar {
  rx: 2; /* 圓角 Bar */
}

.crop-label {
  font-weight: 500;
}



